.workTree {
    display: flex;
    justify-content: center;
    position: relative;
    min-width: 55%;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.particles {
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: #0F0F0F;
    z-index: -1;
}